@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;400;700&display=swap);
body{display:-webkit-flex;display:flex;height:100%;width:100%;margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.light{--violetWhite: #572EE5;--whiteBlack: white;--blackWhite:#373737;--backgroundResults:#E7E7E7

}.dark{--violetWhite: white;--whiteBlack: #373737;--blackWhite:#ffffff;--results:#212121}

*{box-sizing:border-box;margin:0}body,html{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;background-color:var(--whiteBlack);width:100%;height:100%}.App{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;text-align:center;width:100%;height:100%;background-color:var(--whiteBlack)}.cointainer{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;width:900px;min-height:100vh;height:auto;background-color:var(--whiteBlack);border:1px solid #e8e8e8}.search-loading{display:-webkit-flex;display:flex;margin-top:10%;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;font-family:'Roboto', sans-serif;font-style:normal;font-weight:normal;font-size:24px;line-height:42px;color:var(--violetWhite);background-color:var(--whiteBlack)}.background{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;min-width:100vw;width:100%;min-height:100vh;height:auto;background-color:var(--whiteBlack);border:1px solid #e8e8e8}

.header{color:var(--firstColor);background-color:var(--whiteBlack);display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;padding:22px 68px 22px 68px}

.button-lightDark{color:var(--violetWhite);background-color:var(--whiteBlack);border:1px var(--violetWhite) solid;width:148px;height:43px;border-radius:30px;font-family:'Roboto', sans-serif;font-style:normal;font-weight:bold;font-size:14px;line-height:16px;text-align:center}.button-lightDark:hover{box-shadow:inset 0 0 0 3px}

.nav{color:var(--violetWhite);background-color:var(--whiteBlack);display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;width:100%}.title{font-family:'Roboto', sans-serif;font-style:normal;font-weight:normal;font-size:36px;line-height:42px;text-align:center;color:var(--violetWhite)}

.box-search{display:-webkit-flex;display:flex;font-size:20px;width:542px;height:52px;border:1px solid var(--blackWhite);box-sizing:border-box;border-radius:30px 0 0 30px;background-color:var(--whiteBlack);padding-left:20px;color:var(--blackWhite);outline:none}.box-search-active{display:-webkit-flex;display:flex;font-size:20px;width:542px;height:52px;border:1px solid var(--blackWhite);box-sizing:border-box;border-radius:30px 0 0 0;background-color:var(--whiteBlack);padding-left:20px;color:var(--blackWhite);outline:none}.container-search{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;width:628px;height:auto;font-family:Roboto;font-style:normal;font-weight:200;font-size:18px;line-height:21px;text-align:center}.box-button{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;background-color:#572EE5;width:86px;height:52px;border:1px solid #121212;border-radius:0 30px 30px 0}.box-button-active{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;background-color:#572EE5;width:86px;height:52px;border:1px solid #121212;border-radius:0 30px 0px 0}.icon-search{display:-webkit-flex;display:flex}.autocomplete-list{display:inline-block;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:flex-start;justify-content:flex-start;width:100%;list-style:none;padding:0;border:1px solid #d4d4d4;border-radius:0 0 30px 30px}.autocomplete-item{display:-webkit-flex;display:flex;-webkit-justify-content:flex-start;justify-content:flex-start;text-align:left;text-align:initial;justify-content:flex-start;-webkit-align-items:center;align-items:center;-webkit-flex-wrap:wrap;flex-wrap:wrap;width:100%;height:40px;left:0;right:0;padding-left:20px}

.results{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;color:var(--violetWhite);background-color:var(--results);-webkit-justify-content:space-around;justify-content:space-around;-webkit-align-items:center;align-items:center;width:auto;min-height:30%;padding:55px 0 0 0}.search-text{display:-webkit-flex;display:flex;margin-top:10%;margin-bottom:10%;padding:0 24px 0 24px;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;font-family:'Roboto', sans-serif;font-style:normal;font-weight:normal;font-size:24px;line-height:42px;color:var(--violetWhite);background-color:var(--whiteBlack)}

.gifCard{display:-webkit-flex;display:flex;height:250px;width:250px;margin-bottom:41px}.gifImg{display:-webkit-flex;display:flex}

